
import XHeader from 'vux/src/components/x-header/index.vue'

import Progress from "@/components/progress";
import FilterSel from "../../components/filter";
import dayuan from "../../common/js/dayuan.js";
import { mapState } from "vuex";
export default {
data() {
return {
startDate: `${dayjs()
.startOf("month")
.format("YYYY-MM-DD 00:00:00")}`,
endDate: `${dayjs(dayjs().valueOf() + 24 * 60 * 60 * 1000).format(
"YYYY-MM-DD 00:00:00"
)}`,
selUserID: null,
List: [],
Total: 0,
defaultDate: {
beginDate: dayjs()
.startOf("month")
.format("YYYY-MM-DD"),
endDate: dayjs().format("YYYY-MM-DD")
},
types: [
{
type: 1,
name: "选择分类",
notDict: true,
list: [
{
ID: 0,
name: "成交总额"
},
{
ID: 1,
name: "回款总额"
}
]
}
],
endtimers: `${dayjs().format("YYYY-MM-DD 23:59:59")}`,
groupIndex: 0
};
},
computed: {
...mapState(["CurrentUserID"]),
params() {
let userid =
this.selUserID === 0 || this.selUserID
? this.selUserID
: this.CurrentUserID;
return {
startDate: this.startDate,
endDate: this.endDate,
selUserID: userid
};
},
chTitle() {
return this.groupIndex === 0 ? "成交总额 总计：" : "回款总额 总计：";
}
},
filters: {
schedule(count, total) {
if (!count) {
return 0;
} else {
return Number.parseFloat(((count / total) * 100).toFixed(2));
}
},
imgUrl(id) {
return dayuan.aImg(id);
}
},
methods: {

selectTime(date) {
let times = date["beginDate"].replace(/-/g, "/");
let tis = new Date(times).getTime();
let timee = date["endDate"].replace(/-/g, "/");
let tie = new Date(timee).getTime();
if (tis > tie) {
this.$vux.toast.show({
type: "warn",
text: "开始时间不能大于截止时间"
});
return;
}
if (!date["beginDate"]) {
this.$vux.toast.show({
type: "warn",
text: "请选择时间段"
});
return;
}
this.startDate = `${date["beginDate"]} 00:00:00`;
let end = `${timee} 00:00:00`;
end = Number.parseInt(new Date(end).getTime()) + 24 * 60 * 60 * 1000;
this.endDate = dayjs(end).format("YYYY-MM-DD 00:00:00");
this.endtimers = `${date["endDate"]} 23:59:59`;
this.getList();
},
getList() {
this.$vux.loading.show({
text: "加载中"
});
const url = [
`/CustStatis/GetOrderTotalPriceGroupUser`,
`/CustStatis/GetReturnMeonyGroupUser`
];
axios.get(url[this.groupIndex], { params: this.params }).then(res => {
const data = res.data.Data;
this.List = data;
this.Total = data.reduce((i1, i2) => {
return (i1 += i2.Value);
}, 0);
setTimeout(() => {
this.$vux.loading.hide();
}, 500);
});
},

selectUser(userID) {
this.selUserID = userID;
this.getList();
},

changeGroup(v) {
this.groupIndex = v.ID;
this.getList();
},
toDetails(id, size) {
if (!size) {
return;
}
if (this.groupIndex === 0) {
this.$router.push({
path: "/orderDetl",
query: {
createUserId: id,
beginDealDate: this.startDate,
endDealDate: this.endtimers,
size,
name: "订单"
}
});
} else {
this.$router.push({
path: "/orderDetl",
query: {
createUserId: id,
beginReturnDate: this.startDate,
endReturnDate: this.endtimers,
size,
name: "回款"
}
});
}
},
init() {
this.startDate = `${dayjs()
.startOf("month")
.format("YYYY-MM-DD 00:00:00")}`;
this.endDate = `${dayjs(dayjs().valueOf() + 24 * 60 * 60 * 1000).format(
"YYYY-MM-DD 00:00:00"
)}`;
this.defaultDate = {
beginDate: dayjs()
.startOf("month")
.format("YYYY-MM-DD"),
endDate: dayjs().format("YYYY-MM-DD")
};
this.selUserID = 0;
this.groupIndex = 0;
this.$refs["filterSele"].clearType();
this.getList();
}
},
mounted() {
this.getList();
},
watch: {
$route(to, form) {
if (to.path === "/statisIndex") this.init();
}
},
components: {
XHeader,
Progress,
FilterSel
}
};



